import axios from "axios";
import { useEffect, useState } from "react";

import styles from "./css/Register.module.css";

import Input from "./css/component/Input"
import Button from "./css/component/Button"
import Loading from "./css/component/Loading";

const dupButtonStyle = {
  position: "absolute",
  translateY: "-150%",
  translateX: "450%",
}

const Register = () => {
  const [id, setId] = useState("");
  const [idChecked, setIdChecked] = useState(false);
  const [name, setName] = useState("");
  const [nameChecked, setNameChecked] = useState(false);
  const [pw, setPw] = useState("");
  const [pwC, setPwC] = useState("");
  const [email, setEmail] = useState("");

  const [pwStr, setStr] = useState("");
  const [pwCStr, setPwCStr] = useState("");
  const [isPwSame, setIsPwSame] = useState(false);

  const [loading, setLoading] = useState(false);

  const handlers = {
    onChangeId:(e) => {
      setId(e.target.value)
      setIdChecked(false)
    },
    onChangeName:(e) => {
      setName(e.target.value)
      setNameChecked(false)
    },
    onChangePwC:(e) => setPwC(e.target.value),
    onChangeEmail:(e) => setEmail(e.target.value),

    onChangePw(e) {
      if (e.target.value.length < 8) {
        setStr(" 8  .");
        setPw(e.target.value);
        setIsPwSame(false);
      } else if (e.target.value.length > 20) {
        setStr(" 20  .");
        setIsPwSame(false);
      } else {
        //     
        setStr("");
        setPw(e.target.value);
      }
    },

    async checkIdDup(e) {
      e.preventDefault();
      try{
        const res = await axios.post("/api/v1/user/join/id", {loginId:id})
        if(res.data){
          alert("  .")
        } else {
          setIdChecked(true);
        }
      } catch (err) {
        console.log(err.response.data)
      }
    },

    async checkNameDup(e) {
      e.preventDefault();
      try{
        const res = await axios.post("/api/v1/user/join/username", {userName:name})
        if(res.data){
          alert("   .")
        } else {
          setNameChecked(true);
        }
      } catch (err) {
        console.log(err.response.data)
      }
    },

    async submit (e) {
      e.preventDefault();
      setLoading(true)
      if(!isPwSame){
        alert("  .")
        setLoading(false)
      } else if(!idChecked){
        alert("  .")
        setLoading(false)
      } else if(!nameChecked){
        alert("  .")
        setLoading(false)
      } else {
        try{
          const res = await axios.post("/api/v1/user/join", {
            loginId: id,
            loginPw: pw,
            loginPwCheck: pwC,
            userName: name,
            userEmail: email
          });

          window.location.href = "/user/login";
          setLoading(false)
        } catch (err) {
          switch (err.response.status){
            case 409:
              alert("  .");
              break;
            default:
              alert("?");
          }
          setLoading(false)
        }
      }
    }
  }

  useEffect(()=>{
    if (pwC.length>=8) {
      if (pwC!==pw) {
        setPwCStr("  .");
        setIsPwSame(false);
      } else {
        setPwCStr(" .");
        setIsPwSame(true);
      }
    } else {
      setPwCStr("");
      setIsPwSame(false);
    }
  }, [pw, pwC])

  return (
    <div className={styles.register}>
      <form action="">
        <Input
          id="id"
          label=""
          onChange={handlers.onChangeId}
          value={id}
        />
        {!idChecked&&
        <Button
          text={""}
          onClick={handlers.checkIdDup}
          styles={dupButtonStyle}
        />}
        <br />

        <Input
          id="name"
          label=""
          onChange={handlers.onChangeName}
          value={name}
        />
        {!nameChecked&&
        <Button
          text={""}
          onClick={handlers.checkNameDup}
          styles={dupButtonStyle}
        />}
        <br />

        <Input
          id="pw"
          type="password"
          label=""
          onChange={handlers.onChangePw}
          value={pw}
        />
        {pwStr}
        <br />

        <Input
          id="pwC"
          type="password"
          label=" "
          onChange={handlers.onChangePwC}
          value={pwC}
        />
        {pwCStr}
        <br />

        <Input
          id="email"
          label=""
          onChange={handlers.onChangeEmail}
          value={email}
        />
        <br />
        <span
          style={{
            display: "flex",
            justifyContent: "center",
            alignContent: "center",
            width: "50%"
          }}
        >
          {loading?
            <Loading />:
            <Button
              text={""}
              onClick={handlers.submit}
            />
          }
        </span>
        
      </form>
    </div>
  );
}

export default Register;